export default "/* Styling for the navbar component */\r\nnav {\r\n  background-image:url('header-original.jpg');\r\n  background-size:cover;background-position:center;height:20vh;background-attachment:fixed\r\n}\r\n.hamburger {\r\n  background-color:grey\r\n}\r\nnav a {\r\n  display:inline-block;margin-left:10px\r\n}\r\nnav a:link,\r\nnav a:visited {\r\n  padding:8px 8px;color:#fff;transition:border-bottom 0.2s;text-decoration:none;text-transform:uppercase;font-size:90%\r\n}\r\nnav a:hover,\r\nnav a:active {\r\n  border-bottom:2px solid #40bedd\r\n}\r\n/* Medium devices (ipads) #3 */\r\n@media screen and (min-width:767px) and (max-width:900px) {\r\n  nav {\r\n    padding-top:30px;height:20vh\r\n  }\r\n  #navbarCollapse {\r\n    background-color:transparent\r\n  }\r\n  .separator-vertical {\r\n    display:block\r\n  }\r\n}\r\n/* Smallest screens (phones) */\r\n@media screen and (max-width:766px) {\r\n  nav {\r\n    height:auto;position:fixed;width:100%;z-index:99;padding-bottom:10px\r\n  }\r\n  .separator-vertical {\r\n    display:none\r\n  }\r\n  nav a {\r\n    padding:8px;margin-bottom:2px;border-bottom:none;\r\n  }\r\n  nav a:link, nav a:visited {\r\n    border-bottom:none;\r\n  }\r\n  nav a:hover, nav a:active {\r\n    border-bottom:none;color:lightgrey\r\n  }\r\n}\r\n/*# sourceMappingURL=data:application/json;base64, */"